import * as firebase from "firebase/app";
import "firebase/auth";
import React from "react";
import ReactDOM from "react-dom";
import withFirebaseAuth from "react-with-firebase-auth";
import "./styles.css";
const googleButton = require("./googleSignIn.png");

var firebaseConfig = {
  apiKey: "AIzaSyARSneHcKDlC6aFcV3unJ3XfCAZ3Zx8Drw",
  authDomain: "my-jarvis-142e8.firebaseapp.com",
  databaseURL: "https://my-jarvis-142e8.firebaseio.com",
  projectId: "my-jarvis-142e8",
  storageBucket: "my-jarvis-142e8.appspot.com",
  messagingSenderId: "211606203582",
  appId: "1:211606203582:web:be193ab415b64d93"
};
const firebaseApp = firebase.initializeApp(firebaseConfig);
const firebaseAppAuth = firebaseApp.auth();
const providers = {
  googleProvider: new firebase.auth.GoogleAuthProvider()
};
class App extends React.Component {
  renderUserInfo = (user, signOut) => {
    return (
      <div className="user-info-container">
        <div className="user-profile-picture">
          <img
            className="profile-pic"
            src={
              user.providerData
                ? user.providerData[0].photoURL
                : "https://banner2.kisspng.com/20180521/ocp/kisspng-computer-icons-user-profile-avatar-french-people-5b0365e4f1ce65.9760504415269493489905.jpg"
            }
          />
        </div>
        <div className="information">
          <div className="title-text">User Information</div>
          <div className="user-information">
            {/* <div className="info-label">Email:</div> */}
            <div className="info-val">&nbsp;&nbsp;{user.email}</div>
          </div>
          <div className="user-information">
            {/* <div className="info-label">Name:</div> */}
            <div className="info-val">&nbsp;&nbsp;{user.displayName}</div>
          </div>
          <div className="user-information">
            {/* <div className="info-label">Email Verfied:</div> */}
            <div className="info-val">
              &nbsp;&nbsp;
              {user.emailVerified ? "Verfied User" : "Unverfied User"}
            </div>
          </div>
        </div>
        <div onClick={signOut} className="btn-text">
          <img
            src={googleButton}
            width="30px"
            height="30px"
            className="buttton-img"
          />
          &nbsp; Logout
        </div>
      </div>
    );
  };
  render() {
    const { signInWithGoogle, user, signOut } = this.props;
    return this.props ? (
      <div className="App">
        {user ? (
          this.renderUserInfo(user, signOut)
        ) : (
          <React.Fragment>
            <div className="text">
              Sign In With
              <div onClick={signInWithGoogle} className="btn-text">
                <img
                  className="login-btn"
                  src={googleButton}
                  width="30px"
                  height="30px"
                  className="buttton-img"
                />
                &nbsp; Google
              </div>
              to know more
            </div>
            <div className="my-name">Made By: Harish Soni</div>
          </React.Fragment>
        )}
      </div>
    ) : (
      "Loading..."
    );
  }
}
const AppWithFireBase = withFirebaseAuth({
  providers,
  firebaseAppAuth
})(App);
const rootElement = document.getElementById("root");
ReactDOM.render(<AppWithFireBase />, rootElement);
